<template>
    <div>
        <div class="standalone z-index-1" v-if="standalone"></div>
        <div :class="['user','z-index-2',{'b-alone':standalone}]">
            <div class="warp">
                <span class="center-bg"></span>
                <div class="mine" v-link="{name:isLogin ?'user-info':'login',query:{from:from}}">
          <span :style="isLogin && avatar ? 'background-image:url('+ avatar +')' : ''"
                class="img avatar tran-all-3"></span>
                    <!-- <span v-else class="img avatar" style="background-image:url({{avatar | img-host}})"></span> -->
                    <p class="username">{{userName}}</p>
                    <span class="img link-info" v-if="isLogin"></span>
                </div>

                <span class="img msg" v-link="{name:'user-notice'}"></span>
                <span class="unread-tip" v-if="unread"></span>
                <div class="credit-info dis-flex">
                    <div class="flex-1" @click="toIdentify">认证管理</div>
                    <div class="flex-1">信用指数</div>
                </div>
            </div>
        </div>
        <v-content :type="'user-center'">
            <div class="section mg-t10">
                <div class="matter" v-link="{name:'wallet-home'}">
                    <span class="img wallet"></span>
                    <p class="title">我的钱包</p>
                    <span class="img transfer"></span>
                </div>
            </div>
            <div class="section">
                <div class="matter" @click="toTask">
                    <span class="img tool-box"></span>
                    <p class="title">任务管理</p>
                    <span class="img transfer"></span>
                </div>
                <div class="matter" v-link="{name:'skill-show'}">
                    <span class="img skill-show"></span>
                    <p class="title">我的技能秀</p>
                    <span class="img transfer"></span>
                </div>
            </div>
            <div class="section">
                <div class="matter">
                    <span class="img recommend"></span>
                    <p class="title">邀请朋友</p>
                    <span class="img transfer"></span>
                </div>
                <div class="matter">
                    <span class="img service"></span>
                    <p class="title">联系客服</p>
                    <span class="img transfer"></span>
                </div>
                <div class="matter" v-link="{name:'user-set'}">
                    <span class="img set"></span>
                    <p class="title">设置</p>
                    <span class="img transfer"></span>
                </div>
            </div>
        </v-content>

        <v-footer>
        </v-footer>
    </div>
</template>
<script>
    import VContent from '../../components/Content'
    import VFooter from '../../components/Footer'
    import {url, lt, st} from '../../const'
    import {imgHost} from '../../filter'
    import {toast} from '../../assets/js/toast'
    import {DJUtils} from '../../assets/js/common'

    export default{
        route: {
            data({from, to}){
                this.from = to.path;
                this.$broadcast('reload-footer');
                if (lt.has(lt.keys.user)) {
                    return this.$http.get(url.userInfoHome).then(
                        ({data: {success, data, resultMsg}}) => {
                            if (success) {
                                this.isComplete = data.isComplete;
                                if (!data.isLogin) {
                                    lt.remove("user");
                                    this.isLogin = false;
                                    this.userName = '请先登录';
                                    this.unread = false;
                                } else {
                                    this.noticeUnread();
                                    this.isLogin = true;
                                    let userInfo = data.userInfo;
                                    if (userInfo) {
                                        if (userInfo.icon) {
                                            this.avatar = imgHost(userInfo.icon);
                                        } else {
                                            this.avatar = '';
                                        }
                                        //如果有昵称显示昵称，否则显示手机号
                                        if (userInfo.nickName) {
                                            this.userName = userInfo.nickName;
                                        } else {
                                            this.userName = DJUtils.phoneMask(lt.getJson(lt.keys.user).userName);
                                        }
                                    }
                                }
                            } else {
                                toast.alert(resultMsg);
                            }
                        });
                } else {
                    return {
                        isLogin: false,
                        userName: '请先登录',
                        unread: false

                    }
                }
            }
        },
        ready(){
            this.standalone = DJUtils.isStandalone();
        },
        data(){
            return {
                isComplete: false,
                isLogin: false,
                userName: '请先登录',
                avatar: '',
                from: '',
                unread: false,
                standalone: false
            }
        },
        methods: {
            noticeUnread(){
                this.$http.get(url.noticeUnread).then(
                    function ({data:{success, data}}) {
                        if (success) {
                            this.unread = data > 0;
                        }
                    }, function (response) {
                        toast.alert('网络异常');
                    }
                )
            },
            toIdentify(){
                if (!this.isLogin) {
                    window.location.href = "#/login?from=" + this.from;
                    return;
                }
                // if (!this.isComplete) {
                //   toast.alert('请先完善个人信息 再进行实名认证');
                //   window.location.href = "#/user/info";
                //   return;
                // }
                //window.location.href = "#/identify/real/step1";
                window.location.href = "#/identify/index";
            },
            toTask(){
                let isHome = st.getJson(st.keys.menu).isHome;
                let taskType = isHome ? 0 : 1;
                let status = 0;
                this.$http.get(url.taskList, {
                    currentPage: 1,
                    pageSize: 10,
                    t: new Date().getTime(),
                    status: status,
                    taskType: taskType
                }).then(
                    function (response) {
                        let total = response.data.totalResults;
                        if (total == 0) {
                            status = 1;
                        }
                        window.location.href = `#/task?status=${status}&taskType=${taskType}`;
                    }, function (response) {
                        toast.alert('网络异常');
                    }
                )
            }
        },
        components: {VContent, VFooter}
    }


</script>
<style scoped>
    .user {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        height: 1.55rem;
    }

    .user.b-alone {
        top: .4rem;
    }

    .user .warp {
        height: 100%;
        width: 100%;
        position: relative;
    }

    .user .center-bg {
        background-color: #00cda9;
        background-size: 100% 100%;
        height: 100%;
        width: 100%;
        position: absolute;
        z-index: -1;
        /*-webkit-filter: blur(5px);
           -moz-filter: blur(5px);
            -ms-filter: blur(5px);
                filter: blur(5px);*/
    }

    .user .mine {
        margin: 0 auto;
        width: 1.3rem;
        height: 1.05rem;
        position: relative;
    }

    .img.avatar {
        width: .65rem;
        height: .65rem;
        position: absolute;
        top: .15rem;
        left: 50%;
        border-radius: 50%;
        margin-left: -0.325rem;
        background-size: .65rem;
        background-image: url(../../assets/img/user/photo01.png);
    }

    .img.link-info {
        height: .12rem;
        width: .07rem;
        background-size: .07rem;
        position: absolute;
        right: 0;
        top: .55rem;
        background-image: url(../../assets/img/user/arr-right.png);
    }

    .username {
        margin-bottom: .1rem;
        font-size: .14rem;
        color: #fff;
        padding-top: .85rem;
        text-align: center;
    }

    .img.msg {
        height: .22rem;
        width: .22rem;
        background-size: .22rem;
        position: absolute;
        right: .1rem;
        top: .1rem;
        background-image: url(../../assets/img/user/notice.png);
    }

    .credit-info {
        color: #fff;
        text-align: center;
        font-size: .16rem;
        height: .5rem;
        line-height: .5rem;
        margin: 0 .1rem;
        padding-top: .05rem;
    }

    .credit-info div:first-child {
        border-right: .01rem solid #fff;
    }

    .credit-info div {
        margin-top: .1rem;
        height: .2rem;
        line-height: .2rem;
    }

    .section .matter {
        height: .5rem;
        line-height: .5rem;
    }

    .matter .title {
        text-indent: .35rem;
        width: auto;
    }

    .img.transfer {
        top: .2rem;
    }

    .matter .img:first-child {
        width: .25rem;
        height: .25rem;
        background-size: .25rem;
        top: .12rem;
        left: 0;
        position: absolute;
    }

    .img.wallet {
        background-image: url(../../assets/img/user/wallet.png);
    }

    .img.tool-box {
        background-image: url(../../assets/img/user/tool-box.png);
    }

    .img.recommend {
        background-image: url(../../assets/img/user/recommend.png);
    }

    .img.service {
        background-image: url(../../assets/img/user/service.png);
    }

    .img.skill-show {
        background-image: url(../../assets/img/user/skill-show.png);
    }

    .img.set {
        background-image: url(../../assets/img/user/set.png);
    }

    .unread-tip {
        position: absolute;
        width: .07rem;
        height: .07rem;
        border-radius: 50%;
        background-color: #ff3737;
        top: .08rem;
        right: .08rem;
    }

</style>



